    box-sizing: border-box;
}

html, body {
    height: 100%;
}

html {
    font-family: "Segoe UI", "Microsoft YaHei";
    min-width: 1000px;
    min-height: 600px;
}

body {
    position: relative;
    font-size: 14px;
    color: #ffebc8;
    background-color: #161819;
}

body, ul {
    padding: 0;
    margin: 0;
}

main {
    overflow: auto;
    height: 100%;
    padding: 50px 0 30px 0;
}

button, input {
    font-family: inherit;
    border: 0;
    border-radius: 3px;
}

input[type="text"] {
    padding: 0 5px;
    color: #000000;
    background-color: #6e6c49;
    line-height: 24px;
    height: 24px;
    width: 60px;
}
input[type="text"]:focus {
    background-color: #eb8b19;
}
input[type="text"]:disabled {
    background-color: #3e3e3e;
}
input[type="text"]:-moz-read-only {
    background-color: #e0ad70;
}
input[type="text"]:read-only {
    background-color: #e0ad70;
}

button {
    color: #fff;
    height: 24px;
    line-height: 24px;
    text-align: center;
    background-color: #0f898a;
    padding: 0 10px;
}
button:hover {
    background-color: #00acad;
}
button:focus {
    background-color: #046d6e;
    outline: none;
}
button:disabled {
    color: #929292;
    background-color: #636363;
}

.tabs {
    position: relative;
}
.arrow {
    display: inline-block;
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-right: 6px solid #fff;
    border-radius: 10px; 
}

.lm-content-wrap {
    vertical-align: top;
    display: inline-block;
    margin: 10px 20px;
    padding: 10px;
    background-color: #132222;
    box-shadow: 5px 5px 5px #000;
    border-radius: 5px;
}
.lm-content-wrap > h2 {
    background-color: #1a4444;
    color: #fff;
    padding: 5px;
    margin: -10px -10px 10px;
    text-align: center;
    font-size: 14px;
    font-weight: 400;
}



